<template>
    <div id='goods'>
        <div class="goods-search">
            <label for="#"></label>
            <el-input v-model='inputName' placeholder=""></el-input>
            <label for="#"></label>
            <el-select v-model='inputCategory'>
                <el-option v-for="item of inputCategories" :key='item' :value='item'></el-option>
            </el-select>
            <label for="#">
                <el-button type='primary' icon="el-icon-search" round @click="search"></el-button>
            </label>
            <label for="#">
                <el-button type='warning' icon="el-icon-delete" round @click='clear'></el-button>
            </label>
            <label for="#" class="labelRight">
                <el-button type='primary' icon="el-icon-remove-outline" @click='showReduceForm'></el-button>
            </label>
            <label for="#" class="labelRight">
                <el-button type='primary' icon="el-icon-circle-plus-outline" @click='showAddForm'></el-button>
            </label>
        </div>

        <!-- tableData -->
        <el-table :data='goodsData'  style="width:95%;margin:1rem auto;color:" stripe size='small' row-class-name='column-class' empty-text="" element-loading-text="">
            <el-table-column prop='goodsId' label="" width='200' align='center'></el-table-column>
            <el-table-column prop='goodsName' label='' width='200' align='center'></el-table-column>
            <el-table-column prop='goodsCount' label='' width='200' align='center'></el-table-column>
            <el-table-column prop='goodsPrice' label='' width='200' align='center'></el-table-column>
            <el-table-column prop='goodsTime' label='' width='200' align='center'>
                <template #default='scope'>
                    <i class="el-icon-time"></i>
                    <span>{{scope.row.goodsTime}}</span>
                </template>
            </el-table-column>
            <el-table-column prop='goodsCategory' label='' width='200' align='center'>
                <template #default='scope'>
                    <el-tag type='primary' width='100'>
                        {{scope.row.goodsCategory}}
                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column label='' fixed='right' size='small' align='center'>
                <template #default='scope'>
                    <el-button type='primary' plain size='small' @click="showModifyForm(scope.row)"></el-button>
                    <el-button type='danger' plain size='small' @click="deleteRow(scope.row['id'])"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="block">
             <el-pagination
                layout="total,sizes,prev, pager, next, jumper"
                background
                :total="totalCount"
                @size-change='handleSizeChange'
                @current-change='handleCurrentChange'
                :current-page='currentPage'
                :page-sizes='pageSizes'
                :page-size='pageSize'
               >
             </el-pagination>
        </div>

        <!-- addForm -->
        <el-dialog title="" :visible.sync='addFormVisible'   @close="resetForm('addForm')" width='42%'>
            <el-form :model="addForm" :rules="rules" ref='addForm'>
                <el-form-item label='' prop='goodsId' label-width='6rem'>
                    <el-input v-model="addForm.goodsId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsName' label-width='6rem'>
                    <el-input v-model='addForm.goodsName' placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsCount' label-width='6rem'>
                    <el-input v-model.number="addForm.goodsCount" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsPrice' label-width='6rem'>
                    <el-input v-model.number="addForm.goodsPrice" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsCategory' label-width='6rem'>
                    <el-select v-model="addForm.goodsCategory" placeholder="">
                        <el-option  v-for='item of inputCategories' :key='item' :label='item' value='item'></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=' ' prop='goodsTime' label-width='6rem'>
                     <el-date-picker type="date" placeholder="" v-model="addForm.goodsTime"></el-date-picker>
                </el-form-item>
            </el-form>
            <div class="form-button">
                <el-button type='danger' @click='addFormVisible = false'></el-button>
                <el-button type='success' @click="addFormData"></el-button>
            </div>
        </el-dialog>

        <!-- reduceForm -->
        <el-dialog title="" :visible.sync='reduceFormVisible'   @close="resetForm('reduceForm')" width='25%' >
            <el-form :model="reduceForm" :rules="rules" ref='reduceForm'>
                <el-form-item label='' prop='goodsId' label-width='6rem'>
                    <el-input v-model="reduceForm.goodsId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsName' label-width='6rem'>
                    <el-input v-model='reduceForm.goodsName' placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsCount' label-width='6rem'>
                    <el-input v-model.number="reduceForm.goodsCount" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div class="form-button">
                <el-button type='danger' @click='reduceFormVisible = false'></el-button>
                <el-button type='success' @click="reduceFormData"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync=' modifyFormVisible'  @close="resetForm('modifyForm')" width='42%'>
                      <el-form :model="modifyForm" :rules="rules" ref='modifyForm'>
                <el-form-item label='' prop='goodsId' label-width='6rem'>
                    <el-input v-model="modifyForm.goodsId" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsName' label-width='6rem'>
                    <el-input v-model='modifyForm.goodsName' placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsCount' label-width='6rem'>
                    <el-input v-model.number="modifyForm.goodsCount" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsPrice' label-width='6rem'>
                    <el-input v-model.number="modifyForm.goodsPrice" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label='' prop='goodsCategory' label-width='6rem'>
                    <el-select v-model="modifyForm.goodsCategory" placeholder="">
                        <el-option  v-for='item of inputCategories' :key='item' :label='item' value='item'></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label=' ' prop='goodsTime' label-width='6rem'>
                     <el-date-picker  placeholder="" v-model="modifyForm.goodsTime"></el-date-picker>
                </el-form-item>
            </el-form>
            <div class="form-button">
                <el-button type='danger' @click='modifyFormVisible = false'></el-button>
                <el-button type='success' @click="modifyFormData"></el-button>
            </div>
        </el-dialog>

        <!-- dialog -->
        <!-- <el-dialog title="" :visible.sync='deleteRowVisible' center='true' width='25%' custom-class="deleteRow">
            <span></span>
            <span slot='footer'>
                <el-button type='danger' @click="deleteRowVisible = false"></el-button>
                <el-button type='success' @click="deleteRowData"></el-button>
            </span>
        </el-dialog> -->
    </div>
</template>

<script>
export default {
    name:'Goods',
    data(){
        return{
            goodsData:[],//table
            //
            inputName:'',//
            inputCategory:'',//
            inputCategories:['','',''],//
            //
            addFormVisible:false,  //dialog
            modifyFormVisible:false, //dialog
            reduceFormVisible:false, //dialog
            deleteRowVisible:false, //dialog
            //
            addForm:{ //addForm
                goodsId:'',
                goodsName:'',
                goodsNumber:'',
                goodsPrice:'',
                goodsCategory:'',
                goodsTime:''
            },
            modifyForm:{  //modifyForm
                goodsId:'',
                goodsName:'',
                goodsNumber:'',
                goodsPrice:'',
                goodsCategory:'',
                goodsTime:''
            },
            reduceForm:{ //redecueForm
                goodsId:'',
                goodsName:'',
                goodsNumber:''
            },
            //
            rules:{ //form
                goodsId:[
                    { required: true, message: '', trigger: 'blur' },
                    {type:'number',message:'',trigger:'blur'}
                ],
                goodsName:[
                    {required:true,message:'',trigger:'blur'},
                ],
                goodsNumber:[
                    { required: true, message: '', trigger: 'blur' },
                    {type:'number',message:'',trigger:'blur'}
                ],
                goodsPrice:[
                    { required: true, message: '', trigger: 'blur' },
                    {type:'number',message:'',trigger:'blur'}
                ],
                goodsCategory:[
                    {required: true, message: '', trigger: 'change' }
                ],
                goodsTime:[
                    { type: 'date', required: true, message: '', trigger: 'change' }
                ]
            },
            //
            currentPage:1,//
            totalCount:2,//
            pageSizes:[2,4,6,8],//
            pageSize:1//
        };
    },
    methods:{
        search(){
            const searchParams={
                inputName:this.inputName,
                inputCategories:this.inputCategory
            };
            this.$ajax.get('/search',{
                params:searchParams,
                timeout:1000
            })
            .then((response)=>{
                if(response.status==200){
                    this.tableData=response.data;
                }else{
                    this.tableData=[];
                    this.$message.error('');
                }
            })
            .catch((error)=>{
                this.$message.error(error.message);
            })
        },
        clear(){  //
            this.inputName='';
            this.inputCategory='';
        },
        showAddForm(){  //dialog
            this.addFormVisible=true
        },
        showModifyForm(row){//dialog
            this.modifyFormVisible=true;
            this.modifyForm = Object.assign({}, row); //rowmodifyForm
            console.log(this.modifyForm);
        },
        showReduceForm(){
            this.reduceFormVisible = true;
        },
        deleteRow(id){//dialog
            const deleteId=id;
            this.$confirm('','',{
                confirmButtonText: "",
                cancelButtonText: "",
                type:'warning',
            })
            .then(()=>{
                this.$ajax.delete('/deleteRow',{
                    params:{
                        id:deleteId
                    }
                })
                .then(()=>{
                    this.$message({
                        message:'',
                        type:'success'
                    });
                })
                .catch((error)=>{
                    this.$message.error(`${error.message}`);
                })
                
            })
        },
        resetForm(formRef){ //after cancel form
            this.$refs[formRef].resetFields();
        },
        addFormData(){
            const addFormParams=this.addForm;
            this.$ajax.post('/addForm',{
                data:addFormParams,
                timeout:1000
            })
            .then((response)=>{
                this.tableData=response.data;
                this.addFormVisible==true;
                this.$message({
                    message:'',
                    type:'success'
                })
            })
            .catch((error)=>{
                this.$message.error(error.message);
            })
        },
        modifyFormData(){
            const modifyFormParams=this.modifyForm;
            this.$ajax.put('/modifyForm',{
                data:modifyFormParams,
                timeout:1000
            })
            .then((response)=>{
                this.tableData=response.data;
                this.modifyFormVisible=true;
                this.$message({
                    message:'',
                    type:'success'
                })
            })
            .catch((error)=>{
                this.$message.error(error.message);
            })
        },
        reduceFormData(){
            const reduceFormParams=this.reduceForm;
            this.$ajax.put('reduceForm',{
                params:reduceFormParams,
                timeout:1000
            })
            .then((response)=>{
                this.tableData=response.data;
                this.$message({
                    message:''
                })
            })
            .catch((error)=>{
                this.$message.error(`${error.message}`);
            })
        },
        //
        handleSizeChange(value){
            this.pageSize=value;
            this.getData(value,1);
            this.currentPage=1;
        },
        handleCurrentChange(value){
            this.currentPage=value;
            this.getData(this.pageSize,(value)*(this.pageSize));
        },
        getData(pageSize,pageCount){
            this.$ajax.post('https://www.easy-mock.com/mock/5ce5183882fc6a74749c302f/pos/goodsData',{
                orgCode:1,
                   // 
                PageSize:pageSize,
                   // 
                currentPage:pageCount,
            })
            .then((response)=>{
                this.tableData=response.data.body;
                this.totalCount=response.data.body.length;
            })
        }
    },
    created(){
        this.$ajax.get('https://www.easy-mock.com/mock/5ce5183882fc6a74749c302f/pos/goodsData')
        .then((response)=>{
            this.goodsData=response.data;
        })
        .catch((error)=>{
            this.$message({
                message:error.message,
                type:'error'
            });
        });
    }
}
</script>

<style lang="scss">
#goods{
    width: 100%;
    background-color: white;
    height: 100%;
}
.goods-search{
    padding: 2rem;
    border-bottom: 1px dotted black;
    label{
        margin-left: 2rem;
    }
    .el-input{
        width: 12rem;
    }
    .labelRight{
        float: right;
    }
}
.block{
    float: right;
    margin-right:2rem;
}
.el-dialog{
    overflow: hidden;
    height: 25rem;
    .el-input{
        width: 12rem;
    }
    .el-form-item{
        padding: 5px;
        float: left;
    }
    .form-button{
        float: right;
    }
}
.column-class{
 font:14px/1.5 'Microsoft YaHei',arial,tahoma,\5b8b\4f53,sans-serif;
}
.deleteRow{
    height: 29%;
    text-align: center;
}
</style>





